import { Container, Image, Row, Col } from "react-bootstrap";
import SectionTitle from "../LayoutElements/SectionTitle/SectionTitle";
import EducationBlockFon from "./images/education-block-fon.png";
import "./index.scss";

const EducationBlock = () => {
  return (
    <>
      <SectionTitle
        sectionTitleContent="SZKOLENIE"
        sectionTitlePosition={"right"}
      />
      <div className="educationBlock__wrapper">
        <Image
          src={EducationBlockFon}
          style={{ height: "525px", width: "100%", objectFit: "cover" }}
          alt="about firm fon picture"
          fluid
        />
        <Container className="educationBlock__content-wrapper">
          <h5 className="educationBlock__content mt-2 mb-2">
            Oferujemy szkolenia operatorw. Szkolenie zakoczone egzaminem UDT
          </h5>
        </Container>
      </div>
      <Container className="mt-5 mb-5">
        <Row className="pt-3 pb-3">
          <h4 className="educationBlock__title">
            Oferujemy szkolenia operatorw na :
          </h4>
        </Row>
        <Row className="mt-4 mb-4">
          <Col xs={1} className="m-auto">
            <Container className="educationBlock__circule"></Container>
          </Col>
          <Col xs={11}>
            <p className="educationBlock__text">
              Wzki widowe (wszystkie rodzaje : paleciak elektryczny, wzek
              widowy czoowy, wzek widowy boczny, wzek specjalizowany 
              kabina operatora podnoszona wraz z adunkiem).
            </p>
          </Col>
        </Row>
        <Row className="mt-4 mb-4">
          <Col xs={1} className="m-auto">
            <Container className="educationBlock__circule"></Container>
          </Col>
          <Col xs={11}>
            <p className="educationBlock__text">
              Wzkw widowych ze zmiennym wysigiem (adowarki teleskopowe
              "MANITOU").
            </p>
          </Col>
        </Row>
        <Row className="mt-4 mb-4">
          <Col xs={1} className="m-auto">
            <Container className="educationBlock__circule"></Container>
          </Col>
          <Col xs={11}>
            <p className="educationBlock__text">
              Podesty ruchome: przejezdne, wolnobiene, przewone, samojezdne
              montowane na pojedzie (noycowe, koszowe).
            </p>
          </Col>
        </Row>
        <Row className="mt-4 pt-5">
          <p className="educationBlock__footer-text">
            Szkolenie zakoczone egzaminem UDT. Szkolimy rwnie obcokrajowcw.
            Szkolenia s organizowane po wczeniejszym ustaleniu terminu i grupy
            uczestnikw. Przy grupach zorganizowanych moemy zaproponowa
            korzystne rabaty. Szkolenie moemy przeprowadzi na terenie caego
            kraju, po wczeniejszym ustaleniu.
          </p>
        </Row>
      </Container>
    </>
  );
};

export default EducationBlock;
