import SectionTitle from "../LayoutElements/SectionTitle/SectionTitle";
import AccordionItem from "./AccordionItem/AccordionItem";
import "./index.scss";
import { Container, Accordion } from "react-bootstrap";

const QuestionAccordion = () => {
  return (
    <>
      <SectionTitle sectionTitleContent={"FAQ"} sectionTitlePosition={"left"} />
      <Container className="mt-5">
        <Accordion
          defaultActiveKey="0"
          className="questionBlock__accordion"
          flush
        >
          <AccordionItem
            accordionEventKey="0"
            accordionItemQuestionNumber="01"
            accordionItemQuestion="Czy si rni adowarka sztywna od adowarki obrotowej?"
            accordionItemText="adowarka sztywna (czoowa) cechuje si duym udwigiem. Kabina operatora jest nieruchoma, a wysig znajduje si po prawej stronie. Przykadem moe suy adowarka teleskopowa JCB 540-170. adowarka obrotowa posiada gowic obrotow, ktra umoliwia obrt kabiny o 360 stopni. Przykadem moe suy adowarka teleskopowa Manitou MRT 2150."
          />
          <AccordionItem
            accordionEventKey="1"
            accordionItemQuestionNumber="02"
            accordionItemQuestion="Ile kosztuje wynajem podnonika czy adowarki?"
            accordionItemText=" Koszt wynajmu jest uzaleniony od wyboru odpowiedniego urzdzenia oraz okresu wynajmu. Przy wynajmie dugoterminowym cena jest nisza ni przy wynajmie krtkoterminowym."
          />
          <AccordionItem
            accordionEventKey="2"
            accordionItemQuestionNumber="03"
            accordionItemQuestion="Co oznacza 'nalepka UDT' koloru zielonego; umieszczona na podnoniku lub  adowarce?"
            accordionItemText="Taka nalepka umieszczona na urzdzeniu oznacza, e urzdzenie znajduje si pod dozorem technicznym UDT. Kade urzdzenie do prac na wysokoci musi by zarejestrowane w Urzdzie Dozoru Technicznego oraz raz w roku przej przegld. Wraz tak nalepk otrzymuje si dokument (pozytywna decyzja administracyjna), ktry z mocy prawa umoliwia eksploatacj."
          />
          <AccordionItem
            accordionEventKey="3"
            accordionItemQuestionNumber="04"
            accordionItemQuestion="Na co trzeba zwrci uwag przy wyborze wynajmu podnonika noycowego?"
            accordionItemText="Przede wszystkim trzeba odpowiedzie sobie na pytania: gdzie bd wykonywane prace: wewntrz pomieszczenia czy na zewntrz; na jakie wysokoci, czyli na ile metrw potrzebujemy si unie do gry; kady podnonik ma swoje maksymalne obcienie, wic na platforme nie moe pracowa kilka osb naraz."
          />
          <AccordionItem
            accordionEventKey="4"
            accordionItemQuestionNumber="05"
            accordionItemQuestion="Czy uprawnienia UDT z zakresu obsugi oraz konserwacji urzdze technicznych wydawane s bezterminowo?"
            accordionItemText=" Niestety, zgodnie z ustaw z 9 listopada 2018r. o zmianie ustawy o dozorze technicznym zawiadczenia kwalifikacyjne wydane przez UDT przed 1 czerwca 2019 zachowuj wano do 01. 01. 2024. Uprawnienia wane 5 lat.
Uprawnienia trzeba odnawia. W tym celu trzeba byo zoy stosowny wniosek minimum 3 miesice przed wyganiciem uprawnie i udokumentowa, e w ostatnich 5 latach co najmniej przez 3 lata dana osoba wykonywaa prace na stanowisku operatora maszyn. Nie zoenie wniosku w tym terminie spowoduje konieczno ponownego zdawania egzaminu."
          />
        </Accordion>
      </Container>
    </>
  );
};

export default QuestionAccordion;
